<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    v-loading="loading"
    :close-on-click-modal="false"
    append-to-body
    width="1200px"
    @close="close"
  >
    <GoodsSearch :formData="formData">
      <el-button slot="query" size="small" type="primary" @click="handleQuery">
        
      </el-button>
      <el-button slot="reset" size="small" @click="handleReset"></el-button>
    </GoodsSearch>
    <BannerSettingDialogTable
      ref="bannerSettingTable"
      :formData="formData"
      @handleConfirm="handleConfirm"
    ></BannerSettingDialogTable>
  </el-dialog>
</template>

<script>
import GoodsSearch from "../goodsSearch.vue";
import BannerSettingDialogTable from "./table.vue";

const defaultFormData = {
  type: 0, // 0:;1:;2:
  keyword: "", // ////
  partType: "", // 
  productionPlace: "", // 
  vehBrand: "", // 
  brand: "", // 
  priceField: "retail", //  :retail;:alliance;:p;:p1;:p2;:p3;:p4
  priceType: "0", //  1, 2. 3. 4. 5. 0.
  priceValue: "", // 
  partProperty: "", // 
  qtyField: "qty", //  qty:;lockedQty:;orderQty:
  qtyType: "0", //  1, 2. 3. 4. 5. 0.
  qtyValue: "", // 
  image: "0", //  1. 2. 0.
  cleaned: "0" //  1. 2. 0.
};

export default {
  name: "BannerSettingDialog",
  components: {
    GoodsSearch,
    BannerSettingDialogTable
  },
  props: {
    now: Object
  },
  data() {
    return {
      visible: true,
      loading: false,
      formData: JSON.parse(JSON.stringify(defaultFormData))
    };
  },
  methods: {
    hide() {
      this.visible = false;
    },
    handleConfirm(goods) {
      const now = this.now;
      const nowInternalLink = now.internalLink;
      nowInternalLink.name = now.parentPage.name;
      nowInternalLink.code = now.parentPage.code;
      nowInternalLink.sub || (nowInternalLink.sub = {});
      nowInternalLink.sub.name = now.page.name;
      nowInternalLink.sub.code = now.page.code;
      nowInternalLink.sub.skuId = goods.id;
      nowInternalLink.sub.skuName = goods.name;
      nowInternalLink.visible = false;
      this.hide();
    },
    handleQuery() {
      this.$refs.bannerSettingTable.handleQuery();
    },
    handleReset() {
      this.formData = JSON.parse(JSON.stringify(defaultFormData));
    },
    close() {
      this.$emit("hide");
    }
  }
};
</script>
