import request, { SERVICE_MODULE_MAP } from "@/utils/request";

export class StoreManageService {
  // 
  getStoreStatus() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/companies/store-status"
    });
  }

  getBase() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/base"
    });
  }

  // 
  getTemplateList() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/template-list"
    });
  }

  // 
  getModuleList() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/module-list"
    });
  }

  // 
  chooseTemplate(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores",
      method: "post",
      data: params
    });
  }

  // 
  getStoreGuideState() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/got-guide"
    });
  }
  // 
  updateStoreGuideState() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/got-guide",
      method: "post"
    });
  }

  getStoreDetail() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/detail",
      method: "get"
    });
  }
  // 
  updateStore(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/upsert",
      method: "post",
      data: params
    });
  }

  getDomain(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/domain",
      method: "get",
      params
    });
  }

  // 
  getGoods(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: "/goods",
      method: "post",
      data: params
    });
  }
  // 
  getGoodsInfo(id) {
    return request({
      baseURL: SERVICE_MODULE_MAP.united,
      url: "/goods/" + id,
      method: "get",
      params: {
        type: 0
      }
    });
  }

  // 
  getInternalPages(params) {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/companies/pages/internal-pages",
      params
    });
  }

  //
  getShareInfo() {
    return request({
      baseURL: SERVICE_MODULE_MAP.accounts,
      url: "/stores/detail/share"
    });
  }
}
