<template>
  <el-dialog
    title=""
    :visible.sync="visible"
    v-loading="loading"
    :close-on-click-modal="false"
    append-to-body
    width="1200px"
    @close="close"
  >
    <GoodsSearch :formData="formData">
      <el-button slot="query" size="small" type="primary" @click="handleQuery">
        
      </el-button>
      <el-button slot="reset" size="small" @click="handleReset"></el-button>
    </GoodsSearch>
    <GoodsTable
      ref="goodsTable"
      :formData="formData"
      @submit="submit"
      @handleQueryAll="handleQueryAll"
    ></GoodsTable>
    <GoodsTable
      ref="goodsTableInclude"
      type="include"
      :formData="formData"
      @submit="submit"
      @handleQueryAll="handleQueryAll"
    ></GoodsTable>
  </el-dialog>
</template>

<script>
import GoodsSearch from "./goodsSearch.vue";
import GoodsTable from "./goodsTable.vue";

const defaultFormData = {
  type: 0, // 0:;1:;2:
  keyword: "", // ////
  partType: "", // 
  productionPlace: "", // 
  vehBrand: "", // 
  brand: "", // 
  priceField: "retail", //  :retail;:alliance;:p;:p1;:p2;:p3;:p4
  priceType: "0", //  1, 2. 3. 4. 5. 0.
  priceValue: "", // 
  partProperty: "", // 
  qtyField: "qty", //  qty:;lockedQty:;orderQty:
  qtyType: "0", //  1, 2. 3. 4. 5. 0.
  qtyValue: "", // 
  image: "0", //  1. 2. 0.
  cleaned: "0", //  1. 2. 0.

  include: false, // true:;false:
  ids: [], // ID
  respType: "0" // ,0:;1:ID
};

export default {
  name: "ProSettingDialog",
  components: {
    GoodsSearch,
    GoodsTable
  },
  props: {
    now: Object
  },
  data() {
    return {
      visible: true,
      loading: false,
      formData: JSON.parse(JSON.stringify(defaultFormData))
    };
  },
  created() {
    this.formData.ids = this.now.partIds || [];
  },
  methods: {
    submit() {
      const partIds = this.now.partIds;
      partIds.splice(0, partIds.length, ...this.formData.ids);
      this.handleQueryAll();
    },
    handleQueryAll() {
      this.$refs.goodsTable.handleQuery();
      this.$refs.goodsTableInclude.handleQuery();
    },
    handleQuery() {
      this.$refs.goodsTable.handleQuery();
    },
    handleReset() {
      this.formData = JSON.parse(JSON.stringify(defaultFormData));
    },
    close() {
      this.$emit("hide");
    }
  }
};
</script>
