<template>
  <el-form
    class="form-label-top"
    size="small"
    label-position="top"
    label-suffix=""
    :model="data"
  >
    <el-form-item>
      <el-checkbox v-model="data.h5"></el-checkbox>
    </el-form-item>
    <el-form-item label="">
      <el-checkbox
        v-for="(item, index) in typeList"
        :key="index"
        v-model="item.checked"
        :label="item.content"
      ></el-checkbox>
    </el-form-item>

    <el-form-item
      v-for="(item, index) in typeList"
      :key="index"
      :label="item.content"
      v-show="item.checked"
    >
      <el-input
        class="name-input"
        v-model.trim="item.content1"
        maxlength="8"
        placeholder=""
      ></el-input>
      <el-input
        class="des-input"
        v-model.trim="item.content2"
        maxlength="20"
        placeholder=""
      ></el-input>
      <div class="input-explain">{{ item.explain }}</div>
    </el-form-item>

    <el-form-item
      label=""
      v-show="typeList.filter(item => item.checked).length"
    >
      <el-radio-group v-model="radio">
        <el-radio
          v-for="item in typeList"
          :key="item.content"
          :label="item.content"
          v-show="item.checked"
        >
          {{ item.content }}
        </el-radio>
      </el-radio-group>
      <div class="input-explain">
        
      </div>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "SearchSetting",
  props: {
    data: Object
  },
  watch: {
    typeList: {
      handler: function(newValue) {
        let flag = false;
        const list = newValue.filter(item => {
          if (item.checked && !flag) {
            flag = this.radio === item.content;
          }
          return item.checked;
        });
        if (!flag) {
          if (list.length) {
            this.radio = list[0].content;
          } else {
            this.radio = "";
          }
        }
      },
      deep: true
    }
  },
  data() {
    return {
      radio: "",
      typeList: [
        {
          checked: false,
          // disabled: false,
          content: "VIN",
          content1: "VIN",
          content2: "17",
          explain: "17"
        },
        {
          checked: false,
          // disabled: false,
          content: "OE",
          content1: "OE",
          content2: "5",
          explain: "5OE"
        },
        {
          checked: false,
          // disabled: false,
          content: "",
          content1: "",
          content2: "\",
          explain: ""
        },
        {
          checked: false,
          // disabled: false,
          content: "",
          content1: "",
          content2: "",
          explain: ""
        }
      ]
    };
  },
  updated() {
    this.changeData();
  },
  created() {
    let flag = true;
    this.data.content.forEach(item => {
      const nowType = this.typeList.find(type => type.content == item.content);
      if (nowType) {
        if (flag) {
          flag = false;
          this.radio = item.content;
        }
        nowType.checked = true;
        nowType.content1 = item.content1;
        nowType.content2 = item.content2;
      }
    });
  },
  methods: {
    changeData() {
      const list = new Array(1).fill("");
      this.typeList.forEach(item => {
        if (item.checked) {
          if (item.content === this.radio) {
            list[0] = {
              content: item.content,
              content1: item.content1,
              content2: item.content2
            };
          } else {
            list.push({
              content: item.content,
              content1: item.content1,
              content2: item.content2
            });
          }
        }
      });
      this.data.content = list;
    }
  }
};
</script>

<style lang="less" scoped>
.form-label-top {
  .el-form-item {
    .name-input {
      width: 126px;
    }
    .des-input {
      width: 274px;
      margin-left: @margin-size-secondary;
    }
  }
}
</style>
